import { useState } from "react";
import ProfileMenu from "../../components/ProfileMenu";
import { useTranslation } from "react-i18next";
export default function Notifications () {
    const [t,i18n] = useTranslation();
const [notificationsList,setNotificationList] = useState([
    {
        title: ' ',
        message: '         .    ,       !'
    },
    {
        title: ' ',
        message: '         .    ,       !'
    },
    {
        title: ' ',
        message: '         .    ,       !'
    },
    {
        title: ' ',
        message: '         .    ,       !'
    }
]);

    return(<>
        <div className="Profile-Block">
            <ProfileMenu url={'/profile/notifications'}/>
            <div className="Profile-Block__container">
    <h1>{t('notifications')}</h1>
           <div className="Profile-Block__fields">
      <div className="notifications-block">
    {notificationsList.length == 0 ? <p>  </p> : notificationsList.map(elem => <><div>
        <h6>{elem.title}</h6>
        <p>{elem.message}</p>
        </div></>)}
      </div>
      
            </div>
        </div>
        </div>
        
        </>)
}